@tailwind base;
@tailwind components;
@tailwind utilities;

/* Глобальные стили и определения переменных */
:root {
  --bg-color: #FFFFFF;
  --text-color: #000000;

  --bg-color-dark: #000000;
  --text-color-dark: #FFFFFF;

  --btn-bg-color: #4A90E2;
  --btn-text-color: #FFFFFF;
}

html,
body {
  padding: 0;
  margin: 0;
  font-family: sans-serif;
}

body {
  background-color: var(--bg-color);
  color: var(--text-color);
  max-width: 100vw;
  max-height: 100vh;
  overflow: hidden;
}

.app-container {
  display: flex;
  flex-direction: column;
  max-width: 100vw;
  height: 100vh;
  overflow-y: auto;
}

/* Определения для кнопок, используя Tailwind CSS синтаксис как пример */
.btn {
  background-color: var(--btn-bg-color);
  color: var(--btn-text-color);
  padding: 8px 16px;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  font-weight: bold;
}

.btn:hover {
  opacity: 0.9;
}